:root {
  --font-jost: "Jost", sans-serif;
  --font-poppins: "Poppins", sans-serif;
}

:root {
  --gold: hsl(26, 35%, 60%);
  --gray: hsl(240, 1%, 23%);
  --lightgray: hsla(240, 1%, 23%, 0.692);
  --black: hsl(0, 0%, 8%);
  --blueish: hsl(240, 17%, 17%);
  --darkblue: hsl(240, 35%, 9%);
  --blackish: hsl(0, 0%, 8%);
  --darkwhite: hsl(0, 0%, 68%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-jost);
}
@media screen and (max-width: 768px) {
  body {
    overflow-x: hidden;
  }
}

figure {
  margin: 0;
}

.text-ac {
  text-align: center;
}

.flex {
  display: flex;
}
.flex-jc-sa {
  justify-content: space-around;
}
.flex-jc-sb {
  justify-content: space-between;
}
.flex-jc-se {
  justify-content: space-evenly;
}
.flex-jc-fe {
  justify-content: flex-end;
}
.flex-jc-c {
  justify-content: center;
}
.flex-ai-c {
  align-items: center;
}
.flex-fd-c {
  flex-direction: column;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  color: var(--black);
  text-decoration: none;
}

.column-grid-2 {
  margin: 2.5rem auto;
  width: min(95%, 115rem);
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 1.25rem;
}
@media screen and (max-width: 768px) {
  .column-grid-2 {
    column-gap: 0;
    display: grid;
    grid-template-columns: none;
    grid-auto-flow: row;
    width: min(95%, 111rem);
  }
}
.column-grid-2__left-column {
  background-image: url(../../../images/Coffe-shop.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}
@media screen and (max-width: 768px) {
  .column-grid-2__left-column {
    height: 50vh;
  }
}
.column-grid-2__right-column {
  border: 3px solid var(--gold);
}
.column-grid-2__right-column__text {
  margin: 1.875rem;
  font-weight: 400;
  color: var(--blueish);
  font-size: 1.125rem;
  text-transform: capitalize;
}
.column-grid-2__right-column__text h4 {
  color: var(--gray);
  font-size: 2rem;
  margin-bottom: 20px;
}
.column-grid-2__right-column__text .horario {
  color: var(--gray);
  font-size: 2rem;
  margin-bottom: 20px;
  font-weight: 600;
}
.column-grid-2__right-column__buttons__btn-1 {
  background-color: var(--blueish);
  padding: 10px 16px;
  color: white;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 1.2px;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
.column-grid-2__right-column__buttons__btn-1:hover {
  background-color: var(--gold);
}

.gallery {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 5vw);
  row-gap: 0px;
  column-gap: 0px;
}
@media screen and (max-width: 768px) {
  .gallery {
    grid-template-rows: repeat(16, 5vw);
  }
}
.gallery__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-position: center;
}
.gallery__item--1 {
  grid-column-end: 5;
  grid-column-start: 1;
  grid-row-end: 6;
  grid-row-start: 1;
}
@media screen and (max-width: 768px) {
  .gallery__item--1 {
    grid-column-end: 5;
    grid-column-start: 1;
    grid-row-end: 9;
    grid-row-start: 1;
  }
}
.gallery__item--2 {
  grid-column-end: 5;
  grid-column-start: 3;
  grid-row-end: 9;
  grid-row-start: 6;
}
@media screen and (max-width: 768px) {
  .gallery__item--2 {
    grid-column-end: 9;
    grid-column-start: 5;
    grid-row-end: 16;
    grid-row-start: 9;
  }
}
.gallery__item--3 {
  grid-column-end: 9;
  grid-column-start: 5;
  grid-row-end: 9;
  grid-row-start: 1;
}
@media screen and (max-width: 768px) {
  .gallery__item--3 {
    grid-column-end: 9;
    grid-column-start: 5;
    grid-row-end: 9;
    grid-row-start: 1;
  }
}
.gallery__item--4 {
  grid-column-end: 3;
  grid-column-start: 1;
  grid-row-end: 9;
  grid-row-start: 6;
}
@media screen and (max-width: 768px) {
  .gallery__item--4 {
    grid-column-end: 5;
    grid-column-start: 1;
    grid-row-end: 16;
    grid-row-start: 9;
  }
}

.gallery-2 {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(6, 5vw);
  row-gap: 0px;
  column-gap: 0px;
