    grid-column-start: 1;
    grid-row-end: 7;
    grid-row-start: 1;
  }
}
.gallery-2__item--2 {
  grid-column-end: 10;
  grid-column-start: 7;
  grid-row-end: 8;
  grid-row-start: 1;
}
@media screen and (max-width: 768px) {
  .gallery-2__item--2 {
    display: none;
  }
}
.gallery-2__item--3 {
  grid-column-end: 5;
  grid-column-start: 3;
  grid-row-end: 4;
  grid-row-start: 1;
}
@media screen and (max-width: 768px) {
  .gallery-2__item--3 {
    grid-column-end: 9;
    grid-column-start: 5;
    grid-row-end: 7;
    grid-row-start: 1;
  }
}
.gallery-2__item--4 {
  grid-column-end: 3;
  grid-column-start: 1;
  grid-row-end: 7;
  grid-row-start: 1;
}
@media screen and (max-width: 768px) {
  .gallery-2__item--4 {
    display: none;
  }
}
.gallery-2__item--5 {
  grid-column-end: 5;
  grid-column-start: 3;
  grid-row-end: 7;
  grid-row-start: 4;
}
@media screen and (max-width: 768px) {
  .gallery-2__item--5 {
    grid-column-end: 5;
    grid-column-start: 1;
    grid-row-end: 13;
    grid-row-start: 7;
  }
}
.gallery-2__item--6 {
  grid-column-end: 7;
  grid-column-start: 5;
  grid-row-end: 7;
  grid-row-start: 4;
}
@media screen and (max-width: 768px) {
  .gallery-2__item--6 {
    grid-column-end: 9;
    grid-column-start: 5;
    grid-row-end: 13;
    grid-row-start: 7;
  }
}

.gallery-3 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(6, 5vw);
  row-gap: 0px;
  column-gap: 0px;
  margin-top: 6.25rem;
}
.gallery-3__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.gallery-3__item--1 {
  grid-column-end: 2;
  grid-column-start: 1;
  grid-row-end: 6;
  grid-row-start: 1;
}
.gallery-3__item--2 {
  grid-column-end: 3;
  grid-column-start: 2;
  grid-row-end: 6;
  grid-row-start: 1;
}
.gallery-3__item--3 {
  grid-column-end: 3;
  grid-column-start: 4;
  grid-row-end: 6;
  grid-row-start: 1;
}
.gallery-3__item--4 {
  grid-column-end: 5;
  grid-column-start: 4;
  grid-row-end: 6;
  grid-row-start: 1;
}

.topbar {
  width: 100%;
  background-color: var(--black);
  height: 2.5rem;
  font-size: 0.875rem;
}
@media screen and (max-width: 769px) {
  .topbar {
    display: none;
  }
}
.topbar__wrapper {
  height: 100%;
}
.topbar__info {
  color: white;
  letter-spacing: 0.1rem;
  margin-right: 3.75rem;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
.topbar__info:hover {
  color: var(--gold);
}
.topbar__icons {
  margin-right: 3.75rem;
  letter-spacing: 0.1rem;
}
.topbar__icons .phone {
  margin-right: 3.75rem;
}
.topbar__icons .phone .fa-mobile-alt {
  margin-right: 0.5rem;
}
.topbar a {
  color: white;
  transition: all 0.1s ease-in;
}
.topbar a:hover {
  color: var(--gold);
}

.header {
  height: 5rem;
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
@media screen and (max-width: 769px) {
  .header__container {
    display: none;
  }
}
.header__logo img {
  height: 5rem;
}
.header__links {
  flex: 0.3;
  justify-content: space-evenly;
  list-style: none;
  letter-spacing: 0.125rem;
  font-weight: 400;
}
@media screen and (max-width: 500px) {
  .header__links {
    padding-left: 0px;
  }
}
.header a {
  color: var(--blackish);
  transition: all 0.1s ease-out;
}
.header a:hover {
  color: var(--gold);
}
.header__mobile {
  margin: 0 0.625rem;
}
@media screen and (min-width: 770px) {
  .header__mobile {
    display: none;
  }
}
.header__mobile__logo img {
  height: 80px;
}
.header__mobile__nav-links {
  align-items: center;
  background-color: #fff;
  height: 86vh;
  position: absolute;
  left: 0;
  top: 10vh;
  width: 50%;
  z-index: 999;
  padding: 0;
  transform: translateX(-100%);
  transition: transform 0.3s ease-in;
}
.header__mobile__nav-links__container {
  min-height: 18.75rem;
  top: 1.25rem;
  position: absolute;
}
.header__mobile__nav-links__container li {
  list-style: none;
  opacity: 0;
  font-weight: 500;
  letter-spacing: 0.08125rem;
  text-transform: uppercase;
}
.header__mobile__burger {
  position: absolute;
  left: 25px;
  cursor: pointer;
}
.header__mobile__burger div {
  width: 1.5625rem;
  height: 0.1875rem;
  margin: 0.3125rem;
  background-color: var(--gold);
}
.header__mobile .nav-active {
  transform: translateX(0%);
}
@keyframes navLinkFade {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0px);
  }
}

.hero {
  overflow: hidden;
}
.hero__image {
  height: 85vh;
  width: 100vw;
  position: relative;
  background-size: cover;
  background-position: center;
  background-image: url("../../../images/Coffe-shop.jpg");
}
.hero__overlay {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.65);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.hero__overlay h1 {
  font-size: 3rem;
  font-family: var(--font-poppins);
  margin-bottom: 0;
  letter-spacing: 0.075rem;
  font-weight: 500;
  text-transform: capitalize;
  width: 80%;
  text-align: center;
}
.hero__overlay p {
  font-size: 1rem;
}

.entry-text {
  flex-direction: column;
}
.entry-text__wrapper {
  text-align: center;
  width: 32.25rem;
  margin: 4.6875rem 0;
}
@media screen and (max-width: 768px) {
  .entry-text__wrapper {
    margin-bottom: 4.6875rem 0 0.0625rem 0;
    width: 100%;
  }
}
.entry-text__tagline {
  text-transform: uppercase;
  letter-spacing: 0.075rem;
  color: var(--gold);
  font-weight: 600;
}
.entry-text__divider {
  background-color: var(--gold);
  width: 3px;
  height: 3.125rem;
  margin: 0 auto;
}
.entry-text__info {
  font-weight: 400;
  color: var(--gray);
  margin-top: 2.5rem;
}
.entry-text h2 {
  margin-bottom: 1.25rem;
  font-size: 2.375rem;
}

.coffe-menu {
  width: min(95%, 100rem);
  display: grid;
  grid-auto-flow: column;
  background-color: #f7f2ee;
  padding: 1.25rem;
}
@media screen and (max-width: 768px) {
  .coffe-menu {
    grid-auto-flow: row;
  }
}
.coffe-menu__left__header {
  text-align: left;
  text-transform: uppercase;
  color: var(--gray);
  font-size: 1.5rem;
  margin-top: 4rem;
}
.coffe-menu__left__header hr {
  margin-bottom: 30px;
  margin-top: -20px;
  height: 0.3125rem;
  background-color: var(--gray);
}
.coffe-menu__left__body__inner-box {
