@media screen and (max-width: 768px) {
  .coffe-menu__right__box__right-column {
    width: 100%;
  }
}
.coffe-menu__right__box__right-column__item {
  display: flex;
  justify-content: space-between;
  padding-right: 1rem;
  color: white;
  margin-bottom: 1rem;
  font-weight: 600;
  letter-spacing: 0.0625rem;
}
@media screen and (max-width: 768px) {
  .coffe-menu__right__box__right-column__item {
    padding: 0;
  }
}
.coffe-menu__right__box__right-column__item__price {
  color: var(--darkwhite);
}
.coffe-menu__right__bottom-box {
  width: 50%;
  color: white;
  font-size: 0.75rem;
  margin-top: 1rem;
  font-weight: 300;
}

