.footer {
  margin-top: 3.125rem;
}
.footer__container {
  max-width: 100%;
  width: 78rem;
  margin: 0 auto;
}
@media screen and (max-width: 768px) {
  .footer__container {
    flex-direction: column;
    align-items: center;
  }
}
.footer__container__header {
  font-weight: 600;
  text-transform: uppercase;
  font-size: 1.1875rem;
}
.footer__container__links {
  width: 33.33%;
  padding: 24px;
}
.footer__container__links__list {
  padding: 0;
}
.footer__container__links ul {
  list-style: none;
  text-transform: uppercase;
  font-size: 0.875rem;
  letter-spacing: 0.075rem;
  line-height: 2rem;
  font-weight: 500;
}
.footer__container__links a {
